.test-quiz-container {
  overflow: hidden;
  width: calc(100% - #{$base-unit * 2});
  margin: 0 auto;
  background: $color-bg-1;
  box-shadow: $box-shadow;
  border-radius: $base-border-radius;
  margin-bottom: $base-unit;
  line-height: ms-line-height(0);

  @include mq($min-width: $screen-xl) {
    width: 80%;
    max-width: $base-unit * 54;

    .previous-slide & {
      width: calc(100% - #{$base-unit * 2});
    }
  }

  .test-quiz-item {
    min-height: $base-unit * 3;
    display: flex;
    border-radius: 0;

    &:not(:last-of-type) {
      border-bottom: 1px solid currentColor;
    }

    .heading,
    .option {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 ($base-unit * 0.25);
    }

    .heading {
      flex: 2;
      background-color: $color-bg--1;
    }

    .option {
      flex: 1;
      border-left: 1px solid $color-bg--1;

      &.selected,
      &:hover {
        color: currentColor;
        cursor: pointer;

        .tick {
          border-color: currentColor;
        }
      }

      &.selected {
        background-color: white;
      }

      .tick {
        border: 1px solid currentColor;
        border-radius: $base-unit;
        display: inline-block;
        width: $base-unit;
        height: $base-unit;
        vertical-align: middle;
        margin-right: $base-unit * 0.5;
        position: relative;
        line-height: $base-unit * 0.9;

        &:before {
          content: '\2713';
          opacity: 0;
          position: relative;
          font-size: ms(1);
        }
      }

      &:hover .tick:before,
      &.selected .tick:before {
        opacity: 1;
      }
    }
  }
}
